.footer-section {
  .address {
    text-align: center;
    margin-bottom: 120px;
    a {
      display: block;
      color: $color-gray-1;
      border-bottom: 2px solid $color-gray-1;
      width: fit-content;
      margin: 0 auto;
      margin-bottom: 40px;
    }
    p {
      line-height: 1.4;
      font-weight: 500;
      font-size: 28px;
    }
  }
  form {
    @include wrapper;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 80px;
    input {
      margin: 0 15px;
      outline: none;
      border: 0;
      flex: 1;
      border-bottom: 2px solid $color-gray-1;
      padding: 5px;
    }
    h2 {
      font-size: 16px;
      font-weight: normal;
      max-width: 260px;
    }
    button {
      @include call-to-action-button;
      border: none;
      cursor: pointer;
    }
  }
  .bottom-footer {
    background-color: $color-gray-1;
    padding-top: 40px;
    h1 {
      color: $color-gray;
    }
    a {
      color: $color-gray;
    }
    nav {
      @include wrapper;
      display: flex;
      justify-content: space-between;
      width: 100%;
    }
    span {
      display: block;
      text-align: center;
      margin-top: 40px;
      color: $color-gray;
    }
    .socials {
      li {
        color: $color-gray;
      }
    }
  }
}
